import React from 'react'

import "./Platform.css"


function Platform(props) {
    return (
        <div className="d-grid gap-2">

            <button className ="btn btn-primary" type="button" onClick={(e) => {
                window.location.href = props.link;
            }} >
                {props.name}
            </button>

        </div>
    )
}

export default Platform