import React from 'react'
import './user.css';
import Profile from "../Profile/Profile";
import Platform from "../Platform/Platform"
import background from "../../bg_image.jpg"
import Footer from "../Footer/Footer"
//import axios from 'axios'



class User extends React.Component {

    // fetch user and user data

    constructor() {
        super();
        this.state = {
            profilePhoto: {},
            userName: "",
            designation: "",
            links: {
                PhotoshopCourse: "",
                Youtube: "",
                SupportMe: "",
                TelegramChannel: "",
                LinkedIn: "",
                Merhcandise: ""
            },
            socialmedia: {
                gmail: "",
                facebook: "",
                instagram: "",
                twitter: "",
                youtube: "",
                linkedin: "",
                spotify: ""
            }

        }
    }

    componentDidMount() {
        // const url = "/";
        let handle = this.props.match.params.userHandle;
        console.log(handle);
        fetch('/api/' + handle).then(res => res.json()).then(data => handleRes(data));

        let handleRes = data => {
            this.setState({
                profilePhoto: {},
                userName: data.userName,
                designation: data.designation,
                links: {
                    PhotoshopCourse: data.links.PhotoshopCourse,
                    Youtube: data.links.Youtube,
                    SupportMe: data.links.SupportMe,
                    TelegramChannel: data.links.TelegramChannel,
                    LinkedIn: data.links.LinkedIn,
                    Merhcandise: data.links.Merhcandise
                },
                socialmedia: {
                    gmail: data.socialmedia.gmail,
                    facebook: data.socialmedia.facebook,
                    instagram: data.socialmedia.instagram,
                    twitter: data.socialmedia.twitter,
                    youtube: data.socialmedia.youtube,
                    linkedin: data.socialmedia.linkedin,
                    spotify: data.socialmedia.spotify
                }
            })
            console.log(this.state);
        }

        

    }

    //    fetch user and user data
    //  componentDidMount() {
    //     // const url = "/";
    //      console.log('sfs');
    //      axios.get('/api',
    //      ).then(res => console.log(res))
    // }

    render() {
        if (this.props.match.params.userHandle === "amit" || this.props.match.params.userHandle === "designsubway") {
            console.log('here');
            return (

                <div className="user" style={{
                    backgroundImage: `url(${background})`
                }}>

                    <div className="profile">
                        <Profile username={this.state.userName} designation={ this.state.designation}/>
                    </div>

                    <div className="links">
                        <Platform name="Photoshop Course" link = {this.state.links.PhotoshopCourse} />
                        <Platform name="Youtube" link = {this.state.links.Youtube}/>
                        <Platform name="Support Me" link = {this.state.links.SupportMe}/>
                        <Platform name="Telegram Channel" link = {this.state.links.TelegramChannel}/>
                        <Platform name="Linked In" link = {this.state.links.LinkedIn}/>
                        <Platform name="Merchandise" link = {this.state.links.Merhcandise}/>

                    </div>


                    <Footer className="footer" handles = {this.state.socialmedia} />


                </div>



            );
        }
        else {
            return (
                <h1>User not found</h1>
            )
        }

    }



}







export default User;
