import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook, faInstagram, faTwitter, faYoutube, faLinkedin, faSpotify } from '@fortawesome/free-brands-svg-icons'
import { faEnvelope } from '@fortawesome/free-solid-svg-icons'
import "./Footer.css"



function footer(props) {
    console.log(props);
    return (
        <div className="footer">
            <div className="Icons">

                <a href = {props.handles.gmail}>
                    <FontAwesomeIcon className="icon" icon={faEnvelope} />
                </a>
                <a href = {props.handles.facebook}>
                    <FontAwesomeIcon className="icon" icon={faFacebook} />
                </a>
                <a href={props.handles.instagram}>
                    <FontAwesomeIcon className="icon" icon={faInstagram} />
                </a>
                <a href={props.handles.twitter}>
                    <FontAwesomeIcon className="icon" icon={faTwitter} />
                </a>
                <a href={props.handles.youtube}>
                    <FontAwesomeIcon className="icon" icon={faYoutube} />
                </a>
                <a href={props.handles.linkedin}>
                    <FontAwesomeIcon className="icon" icon={faLinkedin} />
                </a>
                <a href={props.handles.spotify}>
                    <FontAwesomeIcon className="icon" icon={faSpotify} />
                </a>
                
                
                
                
                
                
            </div>

            <a href="https://linkrr.in/DESIGNSUBWAY" className="link">DESIGNSUBWAY</a>
        </div>
    )

}

export default footer