import React from 'react'
import './Profile.css'
import Photo from "./../ProfilePhoto/ProfilePhoto"

function Profile(props) {
    console.log(props);
    return (
        <div className="profile">
            <Photo></Photo>
            <div className = "handle">
                <h1>{ props.username}</h1>
                <h2>{props.designation}</h2>
            </div>

        </div>
    )
}


export default Profile